import * as React from "react";
import IconButton from "@mui/material/IconButton";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import TwitterIcon from "@mui/icons-material/Twitter";
import GitHubIcon from "@mui/icons-material/GitHub";
import Container from "@mui/material/Container";

const Footer = () => {
  return (
    <div>
      <p className="home-footer">Made with , React and a lot of patience</p>
      <Container className="container-social-media">
        <a
          href="https://www.linkedin.com/in/pablocappetta/"
          target="_blank"
          rel="noreferrer"
        >
          <IconButton size="small">
            <LinkedInIcon fontSize="inherit" />
          </IconButton>
        </a>
        <a
          href="https://twitter.com/PabloCappetta"
          target="_blank"
          rel="noreferrer"
        >
          <IconButton size="small">
            <TwitterIcon fontSize="inherit" />
          </IconButton>
        </a>
        <a
          href="https://github.com/pablocappetta"
          target="_blank"
          rel="noreferrer"
        >
          <IconButton size="small">
            <GitHubIcon fontSize="inherit" />
          </IconButton>
        </a>
      </Container>
    </div>
  );
};

export default Footer;
