import * as React from "react";
import CssBaseline from "@mui/material/CssBaseline";
import Box from "@mui/material/Box";
import Container from "@mui/material/Container";
import MediaCard from "../components/MediaCard";

export default function Home() {
  return (
    <React.Fragment>
      <CssBaseline />
      <Container className="container-home-page">
        <Box className="box-home-page">
          <Container>
            <h1>Howdy!</h1>
            <Container className="container-profile-photo-description">
              {/* <img
                id="profile-photo"
                src={process.env.PUBLIC_URL + "images/pablo.jpg"}
              /> */}
              <p className="home-fix-text home-profile-description">
                I'm <b>Pablo Cappetta</b>, a 26 years old Front-end Padawan, and
                this is my personal space. There's not that much yet, but it's a
                work in progress, so expect constant changes as time passes. 
              </p>
            </Container>
          </Container>

          <Container>
            <p className="home-liked-stuff-description">
              Meanwhile, I can show you some artists that I like.
            </p>
            <Container className="container-home-media-cards">
              <a
                href="https://open.spotify.com/artist/0L8ExT028jH3ddEcZwqJJ5?si=jc4UMK7-QAGJkMSZY0Knog"
                target="_blank"
                rel="noreferrer"
              >
                <MediaCard
                  img={process.env.PUBLIC_URL + "images/rhcp.webp"}
                  alt={"Recent picture of the Red Hot Chili Peppers members."}
                  title="RHCP"
                  description="The Red Hot Chili Peppers are an American rock band formed in Los Angeles in 1983. Their music incorporates elements of alternative rock, funk, punk rock and psychedelic rock. The band consists of co-founders Anthony Kiedis, Flea, drummer Chad Smith and guitarist John Frusciante."
                  className="container-cards"
                />
              </a>
              <a
                href="https://open.spotify.com/artist/4tZwfgrHOc3mvqYlEYSvVi?si=ZFm4zFIgSuSgvT3dhkfayw"
                target="_blank"
                rel="noreferrer"
              >
                <MediaCard
                  img={process.env.PUBLIC_URL + "images/daftpunk2.jpg"}
                  alt={"Picture of Daft Punk wearing their iconic helmets."}
                  title="Daft Punk"
                  description="Daft Punk were a French electronic music duo formed in 1993 in Paris by Thomas Bangalter and Guy-Manuel de Homem-Christo. Widely regarded as one of the most influential acts in dance music history, they achieved popularity in the late 1990s as part of the French house movement."
                />
              </a>
              <a
                href="https://open.spotify.com/artist/6J7biCazzYhU3gM9j1wfid?si=aYeogvrzSDeWd8WtF3t9_A "
                target="_blank"
                rel="noreferrer"
              >
                {" "}
                <MediaCard
                  img={process.env.PUBLIC_URL + "images/jamiroquai.webp"}
                  alt={
                    "Picture of Jay Kay, the voice of Jamiroquai, singing live and wearing his iconic helmet."
                  }
                  title="Jamiroquai"
                  description="Jamiroquai are an English funk and acid jazz band from London. Formed in 1992, they are fronted by vocalist Jay Kay. They built on their acid jazz sound in their early releases and later drew from rock, disco, electronic and Latin music genres. Lyrically, the group has addressed social and environmental justice."
                />
              </a>
              <a
                href="https://open.spotify.com/artist/2CIMQHirSU0MQqyYHq0eOx?si=D6m7zaj5S5qd5q96BvSVuw"
                target="_blank"
                rel="noreferrer"
              >
                {" "}
                <MediaCard
                  img={process.env.PUBLIC_URL + "images/deadmau5.webp"}
                  alt={
                    "Picture of deadmau5 playing live and wearing his mouse helmet with leds in his eyes and mouth."
                  }
                  title="deadmau5"
                  description="Joel Thomas Zimmerman, known professionally as Deadmau5, is a Canadian electronic music producer, DJ, and musician. He mainly produces progressive house music, though he also produces and DJs other genres of electronic music, including electro house and techno under the alias Testpilot."
                />
              </a>
            </Container>
          </Container>
        </Box>
      </Container>
    </React.Fragment>
  );
}
