import * as React from "react";
import CssBaseline from "@mui/material/CssBaseline";
import Container from "@mui/material/Container";

export default function Portfolio() {
  return (
    <React.Fragment>
      <CssBaseline />
      <Container>
        <h1>Well... this is akward</h1>
        <p className="home-profile-description">
          Here's where I'm supposed to show you my portfolio.{" "}
          <b>If only I had one... </b>
        </p>
      </Container>
      <Container>
        <img
          id="meme"
          src="https://i.gifer.com/7VE.gif"
          alt="John Travolta meme about him being lost"
        />
      </Container>
    </React.Fragment>
  );
}
